body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.header {
  color: #fff;
  background: none;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 20px;
  display: flex;
  position: absolute;
  top: 0;
}

.header .logo {
  color: #fff;
  font-size: 1.5em;
}

.header .logo a {
  color: inherit;
  text-decoration: none;
}

.header .logo a:hover {
  text-decoration: underline;
}

.header nav ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.header nav ul li {
  margin: 0 30px;
  font-size: x-large;
}

.header nav ul li.signup {
  border: 2px solid #fff;
  border-radius: 5px;
  padding: 5px 10px;
}

.header nav ul li.signup a, .header nav ul li a {
  color: #fff;
  text-decoration: none;
}

.list-hot {
  background: url("room.4c8aaa2f.jpg") center / cover no-repeat;
  padding: 50px 20px;
}

.list-hotel-container {
  color: #000;
  opacity: .8;
  text-align: left;
  background-color: #f3f1f1;
  background-size: cover;
  border: 1px solid #030303;
  border-radius: 10px;
  max-width: 600px;
  margin: 50px auto;
  padding: 20px;
  box-shadow: 0 0 10px #0000001a;
}

.list-hotel-container h2 {
  text-align: center;
  margin-bottom: 20px;
}

.list-hotel-container ul {
  padding: 0;
  list-style-type: none;
}

.list-hotel-container li {
  border-bottom: 1px solid #020202;
  padding: 10px 0;
}

.list-hotel-container li h3 {
  margin: 0;
}

.list-hotel-container li p {
  margin: 5px 0;
}

.log-in {
  background: #f0f0f0 url("room.4c8aaa2f.jpg") center no-repeat;
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.login-container {
  text-align: center;
  background-color: #a7a3aa;
  border-radius: 10px;
  padding: 40px;
  box-shadow: 0 4px 8px #0000001a;
}

.login-container h2 {
  margin-bottom: 20px;
}

.login-container h3 {
  color: gray;
  opacity: 1;
}

.login-container form {
  flex-direction: column;
  display: flex;
}

.login-container form div {
  margin-bottom: 15px;
}

.login-container label {
  margin-bottom: 5px;
  display: block;
}

.login-container input {
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 100%;
  padding: 10px;
}

.login-container .pass {
  margin-bottom: 20px;
}

.login-container .error {
  color: red;
  margin-bottom: 15px;
}

.login-container button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  width: 100%;
  padding: 10px;
}

.login-container button:hover {
  background-color: #0056b3;
}

.login-container .sign-up-button {
  background-color: #6c757d;
  margin-top: 15px;
}

.login-container .sign-up-button:hover {
  background-color: #5a6268;
}

.help-supp {
  background: url("room.4c8aaa2f.jpg") center / cover no-repeat;
  padding: 50px 20px;
}

.help-support-container {
  color: var(--hero-text-color);
  text-align: left;
  background-color: gray;
  background-size: cover;
  border: 1px solid #ccc;
  border-radius: 10px;
  max-width: 800px;
  margin: 50px auto;
  padding: 20px;
  box-shadow: 0 0 10px #0000001a;
}

.help-support-container h2 {
  text-align: center;
  margin-bottom: 20px;
}

.help-support-container section {
  margin-bottom: 30px;
}

.help-support-container h3 {
  color: #333;
  margin-bottom: 10px;
}

.help-support-container .faq-item {
  margin-bottom: 15px;
}

.help-support-container .faq-item h4 {
  margin: 0;
}

.help-support-container .faq-item p {
  margin: 5px 0;
}

.help-support-container .support-form .form-group {
  margin-bottom: 15px;
}

.help-support-container .support-form label {
  margin-bottom: 5px;
  font-weight: bold;
  display: block;
}

.help-support-container .support-form input, .help-support-container .support-form textarea {
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 50%;
  padding: 8px;
}

.help-support-container .support-form button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  display: inline-block;
}

.help-support-container .support-form button:hover {
  background-color: #0056b3;
}

:root {
  --hero-bg-color: #ff4081;
  --hero-text-color: #faf9fc;
  --button-bg-color: #3d0e69fa;
  --button-text-color: white;
  --focus-outline-color: #ff408180;
  --toggle-bg-color: #ddd;
  --toggle-active-bg-color: #331c85;
  --toggle-text-color: #fff;
  --toggle-text-color-inactive: #000;
}

.hero {
  color: var(--hero-text-color);
  text-align: left;
  background: url("hero-background.9d2d90bb.jpg") center / cover no-repeat;
  padding: 50px 20px;
}

.hero-content {
  width: 100%;
  max-width: 1200px;
  margin: 0;
  padding-left: 20px;
  font-size: 2.5em;
  line-height: 1;
}

.toggle-buttons {
  justify-content: left;
  margin-top: 20px;
  margin-bottom: 20px;
  display: flex;
}

.toggle-button {
  background-color: var(--toggle-bg-color);
  color: var(--toggle-text-color-inactive);
  cursor: pointer;
  border: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  margin: 0 10px;
  padding: 10px 20px;
  font-size: 1em;
  transition: background-color .3s, color .3s;
  display: flex;
}

.toggle-button.active {
  background-color: var(--toggle-active-bg-color);
  color: var(--toggle-text-color);
}

.toggle-button input {
  display: none;
}

.search-box {
  border-radius: 600px;
  justify-content: center;
  margin-top: 20px;
  padding: 10px 20px;
  display: flex;
  box-shadow: 0 2px 4px #0000001a;
}

.search-box select {
  border: none;
  border-radius: 30px;
  margin: 3px;
  padding: 10px;
  font-size: .5em;
}

.search-box input, .search-box button {
  border: none;
  border-radius: 30px;
  margin: 3px;
  padding: 10px;
  font-size: 1em;
}

.search-box input:focus, .search-box button:focus {
  outline: 2px solid var(--focus-outline-color);
  outline-offset: 2px;
}

.search-box button {
  background: var(--button-bg-color);
  color: var(--button-text-color);
  cursor: pointer;
  transition: background .3s, transform .3s;
}

.search-box button:hover {
  background: darken(var(--button-bg-color), 10%);
  transform: scale(1.05);
}

@media (width <= 768px) {
  .hero-content {
    padding-left: 10px;
    font-size: 2em;
  }

  .search-box {
    flex-direction: column;
    align-items: center;
  }

  .search-box input, .search-box button {
    width: 100%;
    margin: 5px 0;
  }
}

.cities {
  padding: 20px;
}

.cities-grid {
  flex-wrap: wrap;
  justify-content: space-around;
  display: flex;
}

.city-card {
  text-align: center;
  color: inherit;
  margin: 10px;
  text-decoration: none;
}

.city-card img:hover {
  transform: translateY(-10px);
  box-shadow: 12px 12px 12px #0003;
}

.city-card img {
  object-fit: cover;
  border-radius: 10px;
  width: 100px;
  height: 100px;
  transition: transform .3s, box-shadow .3s;
}

@media (width <= 768px) {
  .cities-grid {
    -webkit-overflow-scrolling: touch;
    flex-wrap: nowrap;
    justify-content: flex-start;
    overflow-x: auto;
  }

  .city-card {
    flex: none;
    width: 80px;
    margin: 10px 5px;
  }

  .city-card img {
    border-radius: 50%;
    width: 60px;
    height: 60px;
  }

  .city-card h3 {
    margin-top: 5px;
    font-size: 12px;
    display: block;
  }
}

.benefits {
  text-align: center;
  padding: 20px;
}

.benefits-grid {
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
  margin-top: 20px;
  display: grid;
}

.benefit-card {
  text-align: left;
  border-radius: 10px;
  position: relative;
  overflow: hidden;
  box-shadow: 0 4px 8px #0000001a;
}

.benefit-image {
  border-radius: 10px;
  width: 100%;
  height: auto;
  display: block;
}

.overlay {
  color: #fff;
  text-align: center;
  box-sizing: border-box;
  opacity: 0;
  background: #00000080;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 20px;
  transition: opacity .3s;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.benefit-card:hover .overlay {
  opacity: 1;
}

.overlay h3 {
  margin: 10px 0;
  font-size: 1.5em;
}

.overlay p {
  color: #ddd;
  margin: 10px 0 0;
}

.milestones {
  background: url("milestone.bfb9b7a1.jpg") center / cover no-repeat;
  justify-content: space-around;
  padding: 80px 20px 60px 40px;
  display: flex;
}

.milestone {
  text-align: center;
}

.milestone h2, .milestone p {
  color: #06020a;
  font-size: 2em;
}

.testimonials {
  text-align: center;
  background-image: url("restro.3e9590ef.jpg");
  background-position: center;
  background-size: cover;
  width: 100%;
  padding: 2em;
  position: relative;
  overflow: hidden;
}

.slick-slider {
  width: 100%;
  margin: 0 auto;
}

.slick-prev, .slick-next {
  z-index: 1;
  background-color: #ffffff80;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  display: flex;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.slick-prev:hover, .slick-next:hover {
  background-color: #fffc;
}

.testimonial-slide {
  color: #fff;
  text-shadow: 0 0 10px #00000080;
  background-position: center;
  background-size: cover;
  justify-content: center;
  align-items: center;
  height: 300px;
  display: flex;
  position: relative;
}

.testimonial-content {
  text-align: center;
  background-color: #0009;
  border-radius: 8px;
  max-width: 600px;
  margin: 0 auto;
  padding: 100px;
}

.testimonial-content p {
  margin: 10px 0;
  font-size: 2.2em;
}

.testimonial-content h3 {
  margin-top: 1em;
  font-size: 1.2em;
  font-weight: bold;
}

.slick-slider {
  box-sizing: border-box;
  -webkit-user-select: none;
  user-select: none;
  -webkit-touch-callout: none;
  -khtml-user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
  display: block;
  position: relative;
}

.slick-list {
  margin: 0;
  padding: 0;
  display: block;
  position: relative;
  overflow: hidden;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track, .slick-slider .slick-list {
  transform: translate3d(0, 0, 0);
}

.slick-track {
  margin-left: auto;
  margin-right: auto;
  display: block;
  position: relative;
  top: 0;
  left: 0;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  border: 1px solid #0000;
  height: auto;
  display: block;
}

.slick-arrow.slick-hidden {
  display: none;
}

.slick-loading .slick-list {
  background: #fff url("ajax-loader.32c19c5c.gif") center no-repeat;
}

@font-face {
  font-family: slick;
  font-weight: normal;
  font-style: normal;
  src: url("slick.019ff462.eot");
  src: url("slick.019ff462.eot#iefix") format("embedded-opentype"), url("slick.c8d05265.woff") format("woff"), url("slick.441ccdd6.ttf") format("truetype"), url("slick.0ba9c062.svg#slick") format("svg");
}

.slick-prev, .slick-next {
  cursor: pointer;
  color: #0000;
  background: none;
  border: none;
  outline: none;
  width: 20px;
  height: 20px;
  padding: 0;
  font-size: 0;
  line-height: 0;
  display: block;
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
}

.slick-prev:hover, .slick-prev:focus, .slick-next:hover, .slick-next:focus {
  color: #0000;
  background: none;
  outline: none;
}

.slick-prev:hover:before, .slick-prev:focus:before, .slick-next:hover:before, .slick-next:focus:before {
  opacity: 1;
}

.slick-prev.slick-disabled:before, .slick-next.slick-disabled:before {
  opacity: .25;
}

.slick-prev:before, .slick-next:before {
  opacity: .75;
  color: #fff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: slick;
  font-size: 20px;
  line-height: 1;
}

.slick-prev {
  left: -25px;
}

[dir="rtl"] .slick-prev {
  left: auto;
  right: -25px;
}

.slick-prev:before {
  content: "←";
}

[dir="rtl"] .slick-prev:before {
  content: "→";
}

.slick-next {
  right: -25px;
}

[dir="rtl"] .slick-next {
  left: -25px;
  right: auto;
}

.slick-next:before {
  content: "→";
}

[dir="rtl"] .slick-next:before {
  content: "←";
}

.slick-dotted.slick-slider {
  margin-bottom: 30px;
}

.slick-dots {
  text-align: center;
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
  display: block;
  position: absolute;
  bottom: -25px;
}

.slick-dots li {
  cursor: pointer;
  width: 20px;
  height: 20px;
  margin: 0 5px;
  padding: 0;
  display: inline-block;
  position: relative;
}

.slick-dots li button {
  cursor: pointer;
  color: #0000;
  background: none;
  border: 0;
  outline: none;
  width: 20px;
  height: 20px;
  padding: 5px;
  font-size: 0;
  line-height: 0;
  display: block;
}

.slick-dots li button:hover, .slick-dots li button:focus {
  outline: none;
}

.slick-dots li button:hover:before, .slick-dots li button:focus:before {
  opacity: 1;
}

.slick-dots li button:before {
  content: "•";
  text-align: center;
  opacity: .25;
  color: #000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 20px;
  height: 20px;
  font-family: slick;
  font-size: 6px;
  line-height: 20px;
  position: absolute;
  top: 0;
  left: 0;
}

.slick-dots li.slick-active button:before {
  opacity: .75;
  color: #000;
}

.footer {
  color: #fff;
  text-align: center;
  background-color: #2b0b46;
  padding: 40px 0;
}

.footer-content {
  flex-wrap: wrap;
  justify-content: space-around;
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
}

.footer-section {
  flex: 1;
  min-width: 250px;
  margin: 20px;
}

.footer-section h3 {
  margin-bottom: 20px;
  font-size: 18px;
}

.footer-section p, .footer-section ul, .footer-section a {
  color: #ddd;
  font-size: 20px;
  text-decoration: none;
}

.footer-section a:hover {
  color: #fff;
}

.footer-section ul {
  padding: 0;
  list-style: none;
}

.footer-section ul li {
  margin: 10px 0;
}

.footer-section .social-media {
  justify-content: center;
  align-items: center;
  gap: 20px;
  display: flex;
}

.footer-section .social-media a {
  color: #000;
  text-decoration: none;
  transition: transform .3s;
}

.footer-section .social-media a i {
  color: #ddd;
}

.footer-section .social-media a:hover i {
  color: #fff;
  transform: scale(1.2);
}

.footer-section .social-media a:hover {
  transform: scale(1.2);
}

.footer-section .social-media .fab .fa-facebook, .footer-section .social-media .fab .fa-twitter, .footer-section .social-media .fab .fa-instagram {
  font-size: 3em;
}

.footer-bottom {
  border-top: 1px solid #444;
  margin-top: 20px;
  padding: 20px 0;
  font-size: 14px;
}

@media (width <= 768px) {
  .footer-content {
    flex-direction: column;
    align-items: center;
  }

  .footer-section {
    margin: 10px 0;
  }
}

.sign-up {
  background: url("room.4c8aaa2f.jpg") center no-repeat;
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.sign-up-container {
  text-align: center;
  background: #a7a3aa;
  border-radius: 10px;
  width: 100%;
  max-width: 400px;
  padding: 40px;
  box-shadow: 0 4px 8px #0000001a;
}

.sign-up-container h2 {
  margin-bottom: 20px;
}

.sign-up-container form {
  flex-direction: column;
  gap: 15px;
  display: flex;
}

.sign-up-container label {
  margin-bottom: 5px;
  font-weight: bold;
  display: block;
}

.sign-up-container input {
  border: 1px solid #ddd;
  border-radius: 5px;
  width: calc(100% - 20px);
  padding: 10px;
}

.sign-up-container button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  padding: 10px;
}

.sign-up-container button:hover {
  background-color: #0056b3;
}

.sign-up-container .error {
  color: red;
}

.back-button {
  background-color: #6c757d;
  margin-top: 20px;
}

.back-button:hover {
  background-color: #5a6268;
}

.search-container {
  flex-flow: wrap;
  justify-content: center;
  align-items: flex-start;
  gap: 20px;
  margin-top: 25px;
  padding: 20px;
  display: flex;
}

.search-filter {
  background-color: #f8f8f8;
  border-radius: 10px;
  width: 250px;
  padding: 15px;
  box-shadow: 0 4px 8px #0000001a;
}

.search-filter h2 {
  margin-bottom: 20px;
}

.clear-all-btn {
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 5px;
  width: 100%;
  margin-bottom: 20px;
  padding: 10px;
}

.filter-section {
  margin-bottom: 20px;
}

.filter-section h3 {
  margin-bottom: 10px;
  font-size: 1.2rem;
}

.reset-btn {
  color: #007bff;
  cursor: pointer;
  text-align: left;
  background: none;
  border: none;
  margin-bottom: 10px;
  display: block;
}

.filter-section input[type="text"] {
  border: 1px solid #ddd;
  border-radius: 5px;
  width: 100%;
  margin-bottom: 10px;
  padding: 10px;
}

.filter-list {
  max-height: 200px;
  margin: 0;
  padding: 0;
  list-style: none;
  overflow-y: auto;
}

.filter-list-item {
  align-items: center;
  margin-bottom: 10px;
  display: flex;
}

.filter-list-item input[type="checkbox"] {
  margin-right: 10px;
}

.search-results {
  flex-direction: column;
  flex: 1;
  gap: 20px;
  display: flex;
}

.search-card {
  background-color: #fff;
  border-radius: 10px;
  transition: transform .3s;
  display: flex;
  overflow: hidden;
  box-shadow: 5px 10px 22px #0003;
}

.search-card:hover {
  transform: translateY(-5px);
}

.search-card-image {
  object-fit: cover;
  cursor: pointer;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  width: 50%;
  height: 50%;
}

.search-card-details {
  width: 50%;
  padding: 15px;
}

.search-card-name {
  color: #333;
  margin-bottom: 5px;
  font-size: 1.2rem;
}

.search-card-location {
  color: #666;
  margin-bottom: 10px;
  font-size: 1rem;
}

.search-card-info {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  display: flex;
}

.search-card-rating {
  color: #ffc107;
  font-size: 1rem;
